// import { Heading, Text } from '@chakra-ui/react'
import React from 'react'


import logo from "../logo.jpeg"

import "./About.css"


export const About = () => {
  return (
    <div className="About a" id="about" >
      <div className="a-left">
        <div></div>
        <div>
          <img src={logo} alt="" className="a-img" style={{ width: "500px",borderRadius:"50%" }} />
        </div>
      </div>
      <div className="a-right" style={{height:"300px"}}>
        <div>
          <h1 className="a-title" style={{ marginBottom: "60px"}}>
            About Me
          </h1>
        </div>

       
        <h3 style={{ color: "white" }}>
          Hello ! 👋, I am Prachi Tiwari and I am  enthusiastic Front-end Web
          Developer, skilled in Full Stack Web Development. Having knowledge of
          MERN ,JavaScript, HTML, CSS. High adaptability to learn new
          technologies quickly and paying attention to details.
        </h3>
       
      </div>
    </div>
  );
}


