import React from 'react'
import styled from 'styled-components';
// import APP from "./APP.jpg"

export const Home = () => {
  return (
    <DIV id="home">
      <section className="home">
        <div className="max-width">
          <div className="home-content">
            <div className="text-1">Hello ,my name is</div>
            <div className="text-2">Prachi Tiwari</div>
            <div className="text-3">
              And I'm a <span>Full Stack Web  Developer</span>
            </div>
          </div>

          <div className='logo'>
           
          </div>
        </div>
      </section>
    </DIV>
  );
}

const DIV = styled.div`
  display: flex;
  background-image: url("https://img.freepik.com/free-photo/abstract-futuristic-background-with-3d-design_1361-3532.jpg?w=996&t=st=1679554239~exp=1679554839~hmac=68d9d0d57678fda09fa8e4567d4eed1da1789b8349c64abe28131787c724f60d");
  height: 100vh;
  min-height: 500px;
  /* margin-left:40px; */
  /* margin:aut0 0 auto 40px; */
  /* margin-top:400px; */
  color: white;

  .home > .max-width {
    margin: aut0 0 auto 40px;
    margin-top: 200px;
    margin-left: 50px;
  }
  .home .home-content .text-1 {
    font-size: 27px;
  }
  .home .home-content .text-2 {
    font-size: 56px;
    font-weight: 600;
    /* margin-left: 30px; */
  }
  .home .home-content .text-3 {
    font-size: 40px;
    margin: 5px 0;
  }
  .home .home-content .text-3 span {
    font-size: 27px;
    color: goldenrod;
    font-weight: 500;
  }
  @media (max-width: 947px) {
    /* background-image:url(""); */
    .home .home-content .text-2 {
      font-size:50px;
      font-weight: 600;
      /* margin-left: 30px; */
    }
    .home .home-content .text-3 {
      font-size: 25px;
      margin: 5px 0;
    }
  }
  @media (max-width:500px) {
    .home .home-content .text-2 {
      font-size:40px;
      font-weight: 600;
      /* margin-left: 30px; */
    }
    .home .home-content .text-3 {
      font-size: 25px;
      margin: 5px 0;
    }
  }
`;