import { useEffect, useState } from "react";
import { GoThreeBars } from "react-icons/go";
import "./Navbar.css";

import { HashLink as Link } from "react-router-hash-link";

export const Navbar = () => {
 
  const [name, setname] = useState(false);
  
  const scroll = () => {
    const scrollPosition = window.scrollY; 
    let com = document.getElementById("navComp");
    if (scrollPosition > 20) {
      setname(true);
      com.classList.add("mystyle");
    } else {
      setname(false);
      com.classList.remove("mystyle");
    }
  };
  useEffect(() => {
    scroll();
    window.addEventListener("scroll", scroll);
    return () => {
      window.removeEventListener("scroll", scroll);
    };
  }, []);
  // console.log(hover)
  function handlenav() {
    document.getElementById("myNav").style.height = "100%";
  }

  function closeNav() {
    document.getElementById("myNav").style.height = "0%";
  }

  return (
    <div
      style={{
        padding: name ? "15px 40px" : "",
        fontWeight: "700",
        backgroundColor: name ? "black" : "transparent",
        color: name ? "black" : "",
      }}
      id="nav-menu"
    >
      <a href="/">
        {" "}
        <span class="info-name" style={{ color: "white" }}>
          👨🏻‍💻{" "}
        </span>
      </a>
      <p id="navComp" style={{ color: name ? "black" : "" }}>
        <Link className="nav-link home" to="#home" smooth>
          Home
        </Link>

        <Link className="nav-link about" to="#about" smooth>
          About
        </Link>
        <Link className="nav-link skills" to="#skills" smooth>
          Skills
        </Link>
        <Link className="nav-link projects" to="#projects" smooth>
          Projects
        </Link>
        <Link className="nav-link contact" to="#contact" smooth>
          Contact
        </Link>
        <a
          class="nav-link resume"
          id="resume-button-1"
          onclick="window.open('https://drive.google.com/file/d/1p7Dlcvld0Z2b-_uv2KT_zR-CfX7EhUH0/view?usp=sharing_link')"
          href="#0"
          target="_blank"
          download="Portfolio\Prachi-Tiwari-Resume.pdf"
        >
          <div className="divvv">
            <div>
              <svg
                class="download nav-link resume"
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 512 512"
                style={{ marginLeft: "0", color: "black" }}
              >
                <path d="M288 32c0-17.7-14.3-32-32-32s-32 14.3-32 32V274.7l-73.4-73.4c-12.5-12.5-32.8-12.5-45.3 0s-12.5 32.8 0 45.3l128 128c12.5 12.5 32.8 12.5 45.3 0l128-128c12.5-12.5 12.5-32.8 0-45.3s-32.8-12.5-45.3 0L288 274.7V32zM64 352c-35.3 0-64 28.7-64 64v32c0 35.3 28.7 64 64 64H448c35.3 0 64-28.7 64-64V416c0-35.3-28.7-64-64-64H346.5l-45.3 45.3c-25 25-65.5 25-90.5 0L165.5 352H64zm368 56a24 24 0 1 1 0 48 24 24 0 1 1 0-48z"></path>
              </svg>
            </div>{" "}
            <div>Resume</div>
          </div>
        </a>
      </p>
      <p
        id="mobile"
        onClick={handlenav}
        style={{
          fontSize: "30px",
          color: "white",
          cursor: "pointer",
          float: "right",
          textAlign: "center",
        }}
      >
        <GoThreeBars />
      </p>

      <div id="myNav" className="overlay">
        <a className="closebtn" onClick={closeNav}>
          &times;
        </a>
        <div className="overlay-content">
          <Link to="#home" smooth onClick={closeNav}>
            HOME
          </Link>
          <Link to="#about" smooth onClick={closeNav}>
            ABOUT
          </Link>
          <Link to="#skills" smooth onClick={closeNav}>
            SKILLS
          </Link>
          <Link to="#projects" smooth onClick={closeNav}>
            PROJECTS
          </Link>
          <Link to="#contact" smooth onClick={closeNav}>
            CONTACT
          </Link>

          <a
            class="nav-link resume"
            id="resume-button-1"
            onclick="window.open('https://drive.google.com/file/d/1p7Dlcvld0Z2b-_uv2KT_zR-CfX7EhUH0/view?usp=sharing_link')"
            href="#0"
            target="_blank"
            download="Portfolio\Prachi-Tiwari-Resume.pdf"
          >
            <div className="divvv ggggg"  >
              <div>
                <svg
                  class="download nav-link resume"
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 512 512"
                >
                  <path d="M288 32c0-17.7-14.3-32-32-32s-32 14.3-32 32V274.7l-73.4-73.4c-12.5-12.5-32.8-12.5-45.3 0s-12.5 32.8 0 45.3l128 128c12.5 12.5 32.8 12.5 45.3 0l128-128c12.5-12.5 12.5-32.8 0-45.3s-32.8-12.5-45.3 0L288 274.7V32zM64 352c-35.3 0-64 28.7-64 64v32c0 35.3 28.7 64 64 64H448c35.3 0 64-28.7 64-64V416c0-35.3-28.7-64-64-64H346.5l-45.3 45.3c-25 25-65.5 25-90.5 0L165.5 352H64zm368 56a24 24 0 1 1 0 48 24 24 0 1 1 0-48z"></path>
                </svg>
              </div>{" "}
              <div>Resume</div>
            </div>
          </a>
        </div>
      </div>
    </div>
  );
};

// export default Navbar;
